.App {
    position: fixed;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-image: url("./media/background_hd.jpg");
}

#loading_screen {
    width: 100%;
    height: 100%;
}

.window {
    position: fixed;
    width: 1200px;
    height: 900px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.window-body {
    width: 98.5%;
    height: 95%;
}

.icon_containers {
    padding-top: 1%;
    padding-left: 1%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-weight: bold;
}

.icon {
    padding-top: .5%;
    text-align: center;
    display: block;
    background-color: transparent;
    border: 1px solid transparent;
    margin-right: 10px;
    margin-bottom: 1px;
    float: left;
}

.minesweeper {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

#minesweeper_window {
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 380px;
    height: 450px;
}

#start_message {
    text-align: center;
}

#puzzle {
    text-align: center;
}

#resume_window {
    width: 740px;
    height: 990px;
    display: flex;
    flex-direction: column;
    text-align: center;
}

#resume {
    padding-top: 1%;
    width: 695px;
    height: 900px;
}

.button {
    width: 150px;
    height: 35px;
    font-size: 15px;
}

li {
    padding: .6%;
}

#start_bar {
    bottom: 0;
    left: 0;
    position: absolute;
    background: linear-gradient(#245edb 0%, #3f8cf3 9%, #245edb 18%, #245edb 92%, #333 100%);
    width: 100%;
}